<template>
	<section class="toolbar" >
		<div class="top-bar"

        >
        <div class="expand-button" @click="toggleBottomBar"><i class="fas fa-caret-square-down"></i></div>
			<div class="top-bar-div title-container">{{ songTitle }}</div>
			<div>
                <input
                    v-if="audioPaused === false"
                    class="pause-button"
                    @click="pauseAudio"
                    :disabled="songName === ''"
                    type="button"
                    name="pause-button"
                    value="Pause"
                >
                <input
                    class="play-button"
                    v-if="audioPaused === true"
                    @click="resumeAudio"
                    type="button"
                    name="play-button"
                    value="Resume"
                >
            </div>
		</div>
        <transition name="fade">
		<div class="bottom-bar" v-if="expandBottomBar === true">
			<div>More stuff</div>

			<div class="sort-cell">
                <label for="select-sort">Sort By: </label>
                <select name="" id="" class="select-sort">
                    <option value="">Name</option>
                    <option value="">Genre</option>
                    <option value="">Likes</option>
                    <option value="">Listens</option>
                </select>
            </div>
		</div>
        </transition>
        <div class="audio-container">
            <audio id="audio-player">
                <source  class="source" :src="audioSrc" type="audio/mpeg" />
            </audio>
		</div>
	</section>
</template>

<script>
import EventBus from './eventBus';
	export default {
		name: "ToolBar",
		components: {},
		props: {
            inputData: String,
            songUrl: String
        }    ,
		data() {
			return {
                expandBottomBar: false,
                audioPaused: false,
                songName: '',
                audioSrc: '',
                notification2: "Details...",
                eventInput: this.userInput
			};
		},

		methods: {
            toggleBottomBar() {
                this.expandBottomBar = !this.expandBottomBar;
            },
            receiveUserInput() {
                EventBus.$on('userInputSubmit', data => {
                    console.log(data[0]);
                    this.inputData = data;
                    console.log(this.inputData[0]);
                })
            },
            playAudio() {
                EventBus.$on('songActivated', playSong => {
                const audioPlayer = document.getElementById('audio-player');
                this.songName = playSong[0];
                this.audioSrc = playSong[1];
                audioPlayer.load();
                audioPlayer.play();
                })
            },
            resumeAudio() {
                const audioPlayer = document.getElementById('audio-player');
                audioPlayer.play();
                this.audioPaused = false;
            },
            pauseAudio() {
                const audioPlayer = document.getElementById('audio-player');
                audioPlayer.pause();
                this.audioPaused = true;
            }
        },
		computed: {
            userInput: function() {
                return this.inputData;
            },
            songTitle: function() {
                return this.songName;
            }
        },
		watch: {},
		created() {
            this.playAudio();
        },
		mounted() {},
		updated() {},
		destroyed() {}
	};
</script>

<style scoped>
.toolbar {
    display: grid;
    grid-template-rows: 2;
    width: 97%;
    background: var(--mainRed);
    color: white;
    font-size: 0.8em;
    padding: 2px 0px;
    margin: 0px 5px;
    border: 1px solid rgba(190, 138, 138, 0.226);
    border-right: 1px solid  rgba(141, 60, 60, 0.219);
    border-left: 1px solid  rgba(141, 60, 60, 0.219);
    border-bottom: 1px solid var(--mainRed);
    border-radius: 5px 5px 0px 0px;
    box-shadow:0px 0px 20px 10px inset rgba(136, 61, 61, 0.219);
}


.top-bar {
    display: grid;
    grid-template-columns: 30px 1fr 1fr 80px;
    grid-template-rows: 1;
    padding: 2px 0px 0px 0px;
    font-size: 1em;
    border-radius: 5px 5px 0px 0px;
}
.expand-button {
    padding-left: 3px;
    font-size: 1.5em;
}
.sort-cell>label {
    font-size: 1em;
    font-weight: 400;
    padding:2px 0px 2px 0px;
}
.select-sort {
    min-width: 80px;
    max-width: fit-content;
    /* text-align: center; */
    height: 100%;
    color: rgb(250, 236, 238);
    padding: 2px 0px 2px 0px;
    margin: 0px 0px 0px 6px;
    box-sizing: border-box;
    background: var(--mainRed);
    background:solid rgba(139, 39, 39, 0.856);
    font-size: 1.1em;
    border: 1px solid rgba(139, 39, 39, 0);
    border-bottom: 1px solid rgba(139, 39, 39, 0.856);
}
.title-container {
    grid-column: span 2;
    text-align: left;
        box-sizing: border-box;
    padding: 4px 0px 4px 10px;
    justify-content: center;
    text-align: center;
    overflow: hidden;

}

select>option {
    text-align: center;
}
.bottom-bar {
    width: 100%;
    min-width: fit-content;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: 1;
    padding: 2px 0px 2px 0px;
    margin: 0;
    border-top: 1px solid rgba(90, 10, 10, 0.383);

}
.pause-button {
    background: rgb(47, 83, 149);
    font-size: 1em;
    border: 1px solid rgb(112, 124, 148);
    color: rgb(240, 240, 240);
    padding: 5px 10px;
    border-radius: 3px;
    margin: 3px;
}
.pause-button:disabled {
    background: rgba(37, 89, 131, 0);
    border: 1px solid rgba(189, 189, 189, 0);
    color: rgb(184, 146, 146);
}
.play-button {
    background: rgb(47, 83, 149);
    font-size: 1em;
    border: 1px solid rgb(67, 87, 126);
        border: 1px solid rgb(112, 124, 148);
    color: rgb(240, 240, 240);
    padding: 5px 5px;
    border-radius: 3px;
    margin: 3px;
}
.play-button:disabled {
    background: rgba(37, 89, 131, 0);
    border: 1px solid rgba(189, 189, 189, 0);
    color: rgb(184, 146, 146);
}
.sort-cell {
    grid-column: span 2;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-top: 0px;
    padding-right: 10px;
    background:solid rgba(139, 39, 39, 0.856);


}

</style>